html,body{width:100%;max-width:100%;margin:0}#root{max-width:100%;min-height:100vh;box-sizing:border-box;overflow:auto}body{max-width:100%;overflow:hidden;}body .p-button{border-radius:0 !important}body .container{display:grid;grid-template-columns:repeat(12,1fr);grid-template-rows:auto 1fr;width:100%;height:100vh;}body .container .header{grid-column:span 12}body .container .stripMenu{grid-column:span 1;padding:10px;background:linear-gradient(to right,#200b2d 0%,#2d0f41 100%);}body .container .stripMenu .p-button{min-width:100%;margin-bottom:5px}body .container .main{grid-column:span 7;background:#f4f4f4}body .container .context{grid-column:span 4;border-left:1px solid #c8c8c8;background:#f4f4f4;padding:10px;}body .container .context .p-card{position:fixed;width:30.5vw;}body .container .context .p-card .number-detail{font-size:15px;font-weight:100;line-height:1.25rem;margin-top:1em;margin-bottom:0;}body .container .context .p-card .number-detail strong{font-weight:900}body .container .context .p-card table.p-card-table,body .container .context .p-card th,body .container .context .p-card td{border:none}body .container .context .p-card table.p-card-table{margin-top:1em;width:100%;border-collapse:collapse;}body .container .context .p-card table.p-card-table tr{background:#f3a8a1}body .container .context .p-card table.p-card-table tr:first-child td{padding:6px 6px 0 6px}body .container .context .p-card table.p-card-table td{border-spacing:0;padding:6px}body .container .context .p-card table.p-card-table td.right{text-align:right;font-weight:100;background:#f9f9f9}body .container .context .p-card table.p-card-table td.center{padding-left:.5em;background:#fdf1e7}body .p-menubar{border-top:none;border-right:none;border-left:none;border-radius:0}body .p-datatable{border-radius:0 !important;}body .p-datatable .p-paginator,body .p-datatable .p-datatable-header,body .p-datatable th:first-child,body .p-datatable td:first-child,body .p-datatable th:last-child,body .p-datatable td:last-child{border-right:none !important;border-left:none !important}body .p-datatable .p-datatable-header{border-top:none}body .p-datatable .p-button-text,body .p-datatable td{font-size:13px;line-height:15px}body .p-datatable .pi-search{margin:4px 10px 0 0 !important}body .p-datatable .p-dropdown{margin-top:4px}body .p-datatable .p-datatable-header{border-radius:0 !important}body .p-datatable .p-datatable-thead th span,body .p-datatable .p-datatable-thead .p-dropdown-label{font-weight:200;font-size:11.5px}body .p-datatable .p-datatable-thead .p-column-title{display:inline-block;line-height:13px}body .p-datatable .p-datatable-thead .p-sortable-column-icon{display:block;clear:both;text-align:center;margin-top:3px;width:100%}body .mapPlaceholder{max-width:100%;position:relative;}body .mapPlaceholder img{position:absolute;max-width:100%}body .mapPlaceholder .caption{position:absolute;top:0;right:0;padding:3px 6px 4px 12px;background:#333;color:#fff;font-family:"Open Sans","Helvetica Neue",sans-serif;font-size:14px}